<template>
  <div id="map" class="h-100"></div>
</template>

<script>
import maplibregl from 'maplibre-gl'; 
import OGCFeatureCollection from 'mapbox-gl-ogc-feature-collection'

export default {
  name: 'MapView',
  mounted(){
    const map = new maplibregl.Map({
      container: 'map',
      style: {
                'version': 8,
                'sources': {
                    'raster-tiles': {
                        'type': 'raster',
                        'tiles': [
                            'https://tile.openstreetmap.org/{z}/{x}/{y}.png'
                        ],
                        'tileSize': 256,
                        'attribution': '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    }
                },
                'layers': [
                    {
                        'id': 'simple-tiles',
                        'type': 'raster',
                        'source': 'raster-tiles',
                        'minzoom': 0,
                        'maxzoom': 22
                    }
                ]
            },
      center: [-95, 40],
      zoom: 3
    });

    map.addControl(new maplibregl.NavigationControl())

    map.on('load', () => {

      const sourceId = 'featureserver-src'

      new OGCFeatureCollection(sourceId, map, {
          url: 'https://demo.pygeoapi.io/stable',
          collectionId: 'lakes',
          limit: 10000
      })

      map.addLayer({
          'id': 'lyr',
          'source': sourceId,
          'type': 'fill',
          'paint': {
              'fill-color': '#B42222',
              'fill-opacity': 0.7
          }
      })

      var popup = new maplibregl.Popup();

      map.on('mouseenter', 'lyr', function () {
        map.getCanvas().style.cursor = 'pointer';
      })

      map.on('click', 'lyr', function (e) {
        map.getCanvas().style.cursor = 'pointer';
        
        var description = `ID: ${e.features[0].id}<br>Name: ${e.features[0].properties.name}`
        
        popup.setLngLat(e.lngLat).setHTML(description).addTo(map);
      });
        
      map.on('mouseleave', 'lyr', function () {
        map.getCanvas().style.cursor = '';
      });
    })
  }
}

</script>

<style scoped>

.h-100{
  height: 100vh;
}
</style>